.pseudo {
  content: "";
  position: absolute;
}
.reset-list {
  margin: 0;
  padding: 0;
  list-style: none;
}
.visually-hidden {
  position: absolute;
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0;
  border: 0;
  width: 1px;
  height: 1px;
  overflow: hidden;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
html {
