.header__top {
  position: relative;
  display: flex;
  justify-content: space-between;
  padding: 10px 0;
}
.header__logo {
  display: block;
  width: 80px;
  height: 30px;
}
.header__button {
  position: absolute;
  top: 50%;
  right: 0;
  width: 30px;
  height: 20px;
  transform: translateY(-50%);
}
.header__burger {
  position: absolute;
  top: 50%;
  left: 0;
  display: block;
  width: 100%;
  height: 2px;
  background-color: #000000;
  transform: translateY(-50%);
  transition: 0.001s;
}
.header__burger::before,
.header__burger::after {
  content: "";
  position: absolute;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #000000;
  transition: 0.3s;
}
.header__burger::before {
  transform: translate(0, -8px);
}
.header__burger::after {
  transform: translate(0, 8px);
}
.header__burger--active {
  background-color: transparent;
}
.header__burger--active::before {
  transform: rotate(45deg);
}
.header__burger--active::after {
  transform: rotate(-45deg);
}
.header__contacts {
  padding: 10px 0;
  background-color: #f5f5f5;
}
.header__contacts--hidden {
  display: none;
}
.header__contacts-text {
  font-weight: 500;
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 14px;
  line-height: 24px;
  color: #696969;
  margin: 0;
  margin-bottom: 5px;
  padding: 0;
}
.header__contacts-info {
  margin: 0;
  padding: 0;
  list-style: none;
}
.header__contacts-info li {
  position: relative;
  padding-left: 28px;
}
.header__contacts-info li:nth-child(1)::before {
  background-image: url("../img/mail-icon.png");
  background-position: -1px -1px;
}
.header__contacts-info li:nth-child(2)::before {
  left: 5px;
  background-image: url("../img/phone-icon.png");
  background-position: 1px -1px;
}
.header__contacts-info li::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 0;
  width: 22px;
  height: 16px;
  background-repeat: no-repeat;
  transform: translateY(-50%);
}
.header__contact {
